// - - - - - - - - - - - - - - - - - -

// Single page/post/project

// - - - - - - - - - - - - - - - - - -

.intro {
	
	h1 {
		
		@include mq(tabletp) {
			white-space: pre-line;
		}
	}

	p {

	}
}

.content-wrap {

	@include mq(tabletp) {
		max-width: 90%;
		margin: 0 auto;
	}
}

.single {
		
	margin-top: 50px;
	
	h1, h2, h3, h4, h5, h6, p, ul, ol {
		margin-left: auto;
		margin-right: auto;
		padding-left: 30px;
		padding-right: 30px;
		max-width: 500px;
		text-align: center;
		text-decoration: none;
		@include mq(tabletp) {
			padding-left: 0;
			padding-right: 0;
			max-width: 70%;
		}

		@include mq(tabletl) {
			max-width: 65%;
		}

		@include mq(laptop) {
			max-width: 60%;
		}
	}

	hr {
		margin: 30px auto;
		width: calc(100vw - 60px);
		max-width: 440px;

		@include mq(tabletp) {
			margin-top: 40px;
			margin-bottom: 40px;
			margin-left: auto;
			margin-right: auto;
			width: auto;
			max-width: 70%;
		}

		@include mq(tabletl) {
			max-width: 65%;
		}

		@include mq(laptop) {
			max-width: 60%;
		}
	}

	blockquote,
	figure {
		max-width: 90%;
		margin: 20px auto;

		@include mq(tabletp) {
			margin: 30px auto;
		}

		@include mq(laptop) {
			margin: 40px auto;
		}
	}

	blockquote p {
		max-width: 100%;
		padding: 0;
	}

	p a {
		font-weight: $bold-weight;
		border-bottom: 2px solid $accent-color;
	}
    
	p .button {
		margin-top: 10px;
	}

	.highlighter-rouge,
	table {
		margin: 20px auto;
		width: 100%;
		max-width: 90%;

		@include mq(tabletp) {
			margin: 30px auto;
			max-width: 70%;
		}

		@include mq(tabletl) {
			max-width: 65%;
		}

		@include mq(laptop) {
			margin: 40px auto;
			max-width: 60%;
		}
	}

	.highlight {
		display: block;
		padding: 0;
		line-height: 1.5;
		font-size: $p-small*0.85;
		line-height: $p-lineheight;
		overflow: auto;

		@include mq(tabletp) {
			font-size: $p-medium*0.85;
		}

		@include mq(laptop) {
			font-size: $p-large*0.85;
		}

		.highlight {
			padding: 20px;

			@include mq(tabletp) {
				padding: 30px;
			}

			@include mq(laptop) {
				padding: 40px;
			}
		}
	}

	table {
		border: 2px solid $text-dark-color;

		thead {
			background: $text-dark-color;
			color: $background-color;
			text-align: left;
			font-weight: $bold-weight;

			th {
				font-weight: $bold-weight;
			}
		}

		th, td {
			padding: 10px;
			font-size: $p-small;
			font-weight: $p-weight;

			@include mq(tabletp) {
				padding: 15px;
				font-size: $p-medium;
			}

			@include mq(laptop) {
				padding: 20px;
				font-size: $p-large;
			}
		}
		
		tr {

			&:nth-child(2n) {
				background: $background-alt-color;
			}
		}
	}

	sup a.footnote {
		display: inline-block;
		margin-left: 5px;
		min-width: 16px;
		height: 16px;
		border-radius: 100%;
		border-bottom: none;
		padding: 1px 3px;
		background: $background-alt-color;
		font-size: 10px;
		text-align: center;
		color: $text-light-color;

		&:hover {
			background: $text-light-color;
			color: $background-color;
		}
	}

	.footnotes {
		margin-top: 40px;

		@include mq(tabletp) {
			margin-top: 60px;
		}

		@include mq(laptop) {
			margin-top: 80px;
		}

		ol {
			list-style: none;
			counter-reset: footnotes;

			li {
				margin-top: 5px;
				font-size: 13px;
				counter-increment: footnotes;

				&:before {
					content: counter(footnotes);
				    box-sizing: border-box;
					display: inline-block;
					margin-right: 3px;
					width: 20px;
					height: 20px;
					border-radius: 100%;
					border-bottom: none;
					padding: 2px 3px;
					background: $background-alt-color;
					font-size: 11px;
					font-weight: $bold-weight;
					text-align: center;
					color: $text-light-color;
				}
				
				p {
					display: inline;
					max-width: 100%;
					font-size: 13px;

					a.reversefootnote {
						border-bottom: 0;
						vertical-align: sub;
					}
				}
			}
		}
	}
}

.image-wrap,
.video-wrap {
	position: relative;
	max-width: 100%;
	margin-top: 90px;
	margin-bottom: 10px;

	@include mq(tabletp) {
		margin: auto;
		margin-top: 90px;
		margin-bottom: 10px;
	}

	@include mq(laptop) {
		margin: auto;
		margin-top: 90px;
		margin-bottom: 10px;
	}

	&:first-child {
		margin-top: 0;
	}
}

.image-wrap img {
	margin: 0 auto;
}

.video {
	position: relative;
	width: 100%;
	height: 0;

	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

iframe {
	display: block;
	outline: none;
	margin: auto;
}

.pagination {
	flex-wrap: wrap;
	text-align: center;
	margin-top: auto;
}

.pagination span {
	padding: $grid-spacing;
}

.page__related {
	padding-top: $grid-spacing;
}

.page__related-title {
	padding: 0;
	text-align: left;
	padding: $grid-spacing $grid-spacing*4 $grid-spacing*2;
}

#top {
	display: none;
	position: fixed;
	text-align: center;
	width: 60px;
	height: 60px;
	bottom: 50px;
	right: 1%;
	z-index: 999;
	font-size: 16px;
	border: none;
	border-radius: 20px;
	outline: none;
	background-color: $border-color ;
	color: $text-dark-color;
	cursor: pointer;
	padding: 15px;
	border-radius: 4px;
  }

#top:hover {
	background-color: $accent_color;
  }